import React, { useState } from "react";
import { Link, Navigate, useNavigate } from "react-router-dom";
import { Form, Alert } from "react-bootstrap";
import { Button } from "react-bootstrap";
import { useUserAuth } from "./UseAuthContext";

const Signup = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [name, setName] = useState("");
  const [error, setError] = useState("");
  const { signUp } = useUserAuth();
  let navigate = useNavigate();

//   const handleSubmit = async (e) => {
//     e.preventDefault();
//     setError("");
//     try {
//       await signUp(email, password);
//       navigate("/login");
//     } catch (err) {
//       setError(err.message);
//     }

const handleSubmit = async (e) => {
    e.preventDefault();
    setError("");
    fetch('http://localhost:3007/signUp',{
        method:"POST",
        headers:{
          'content-type':'application/json'
        },
        body:JSON.stringify({
            name:name,
            email:email,
            password:password
        })
      })
      .then(response=>{
          response.json();
      })
      .then(result=>{
        console.log(result);
    })
    .catch(err=>{
        console.log(err);
    })
    
    navigate('/login')

  };

  return (
    <>
    <div className="p-5 d-flex mt- box " >

      <div className="p-4 box" style={{width:'30%',marginLeft:'35%',border:'1px solid black'}}>
        <h2 className="mb-3">Sign Up </h2>
        {error && <Alert variant="danger">{error}</Alert>}
        <Form onSubmit={handleSubmit}>
        <Form.Group className="mb-3" controlId="formBasicname">
            <Form.Control
              type="text"
              placeholder="Name"
              onChange={(e) => setName(e.target.value)}
            />
          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Control
              type="email"
              placeholder="Email address"
              onChange={(e) => setEmail(e.target.value)}
            />
          </Form.Group>

          <Form.Group className="mb-3" controlId="formBasicPassword">
            <Form.Control
              type="password"
              placeholder="Password"
              onChange={(e) => setPassword(e.target.value)}
            />
          </Form.Group>

          <div className="d-grid gap-2" style={{marginLeft:'14%'}}>
            <Button variant="primary" type="Submit">
              Sign up
            </Button>
          </div>
        </Form>
      </div>
      <div className="p-4 box mt-3 text-center">
        Already have an account? <Link to="/login">Log In</Link>
      </div>

    </div>
    </>
  );
};

export default Signup;
